<template>
  <div class="main-container">
    <TableHeader :can-collapsed="false">
      <template slot="right">
        <el-button
          type="danger"
          size="mini"
          icon="el-icon-delete"
          @click="onDeleteItems(null)"
        >
        </el-button>
      </template>
    </TableHeader>
    <TableBody ref="tableBody">
      <template slot="table">
        <el-table
          ref="table"
          v-loading="tableLoading"
          :data="dataList"
          :size="tableConfig.size"
          :stripe="tableConfig.stripe"
          :border="false"
          :height="tableConfig.height"
          @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="45"
          />
          <el-table-column
            align="center"
            label=""
            width="80"
          >
            <template slot-scope="scope">
              {{ scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
            prop="nickName"
          />
          <el-table-column
            align="center"
            label=""
          >
            <template slot-scope="scope">
              <div class="avatar-container">
                <el-image
                  :src="require('@/assets/img_avatar_01.jpeg')"
                  class="avatar"
                  :class="{'avatar-vip' : scope.row.vip === 1}"
                />
                <img
                  v-if="scope.row.vip === 1"
                  class="vip"
                  :src="require('@/assets/img_vip_icon.png')"
                />
              </div>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
            prop="gender"
          >
            <template slot-scope="scope">
              <div class="gender-container flex justify-center align-center">
                <img
                  class="gender-icon"
                  :src="scope.row.gender === 0 ? require('@/assets/icon_sex_man.png') : require('@/assets/icon_sex_woman.png')"
                />
                <span>{{ scope.row.gender === 0 ? '' : '' }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
            prop="address"
          />
          <el-table-column
            align="center"
            label=""
            prop="lastLoginTime"
            width="160px"
          />
          <el-table-column
            align="center"
            label="IP"
            prop="lastLoginIp"
            width="130px"
          />
          <el-table-column
            align="center"
            label=""
          >
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.status"
                :active-value="1"
                :inactive-value="0"
              />
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
          >
            <template slot-scope="scope">
              <el-link
                type="danger"
                :underline="false"
                @click="onDeleteItems(scope.row)"
              ></el-link>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </TableBody>
    <TableFooter
      :current-page="pageModel.currentPage"
      :page-size="pageModel.pageSize"
      :total-size="pageModel.totalSize"
      @pageSizeChanged="pageSizeChanged"
      @currentChanged="currentChanged"
    />
  </div>
</template>

<script>
import TableMixin, { PageModelMixin } from '@/mixins/TableMixin'
import { GetDataMixin, DeleteItemsMixin } from '@/mixins/ActionMixin'
export default {
  name: 'Table',
  mixins: [TableMixin, PageModelMixin, GetDataMixin, DeleteItemsMixin],
  data() {
    return {
      userModel: {}
    }
  },
  mounted() {
    this.initGetData({
      url: this.$urlPath.getTableList,
      params: this.withPageInfoData(),
      multiParams: (item) => {
        return {
          ids: item.map((it) => it.id).join(',')
        }
      },
      onResult: (res) => {
        this.handleSuccess(res)
      },
      afterAction: (res) => {
        console.log(res)
      }
    }).then((_) => {
      this.getData()
    })
    this.initDeleteItems({
      url: this.$urlPath.getTableList,
      params: () => {
        return { id: this.userMode.id }
      },
      multiParams: (items) => {
        return {
          ids: items.map((it) => it.id).join(',')
        }
      },
      onDeleteItems: (item) => {
        if (!item && +this.items === 0) {
          this.$errorMsg('')
          return
        }
        this.userMode = item
        this.$showConfirmDialog('').then((_) => {
          this.doDeleteItems(item)
        })
      },
      onResult: (res) => {
        this.$successMsg('')
      },
      onError: (error) => {
        this.$errorMsg(error)
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.avatar-container {
  position: relative;
  width: 30px;
  margin: 0 auto;
  vertical-align: middle;
  .avatar {
    width: 100%;
    border-radius: 50%;
  }
  .avatar-vip {
    border: 2px solid #cece1e;
  }
  .vip {
    position: absolute;
    top: 0;
    right: -9px;
    width: 15px;
    transform: rotate(60deg);
  }
}
.gender-container {
  .gender-icon {
    width: 20px;
  }
}
</style>
