<template>
  <div class="main-container">
    <el-card :body-style="{padding: '5px'}">
      <template #header>
        <div class="flex justify-between">
          <el-link :underline="false"></el-link>
          <el-button
            type="primary"
            size="small"
            @click="printImage"
          ></el-button>
        </div>
      </template>
      <div class="image-wrapper">
        <img :src="imagePath" />
      </div>
    </el-card>
    <el-card
      :body-style="{padding: '5px'}"
      class="margin-top-xs"
    >
      <template #header>
        <div class="flex justify-between">
          <el-link :underline="false">HTML</el-link>
          <el-button
            type="primary"
            size="small"
            @click="printHtml"
          ></el-button>
        </div>
      </template>
      <div
        id="htmlWrapper"
        class="html-wrapper flex justify-center align-center flex-direction"
      >
        <el-table
          :data="dataList"
          :header-cell-style="tableConfig.headerCellStyle"
          :size="tableConfig.size"
          :stripe="tableConfig.stripe"
          :border="tableConfig.border"
          :height="tableConfig.height"
        >
          <el-table-column
            align="center"
            label=""
            prop="name"
          />
          <el-table-column
            align="center"
            label=""
            prop="age"
          />
          <el-table-column
            align="center"
            label=""
            prop="gender"
          />
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import printJS from 'print-js'
import imagePath from '@/assets/img_vue.jpeg'
import TableMixin from '@/mixins/TableMixin'
export default {
  name: 'Print',
  mixins: [TableMixin],
  data() {
    return {
      imagePath,
      dataList: [
        {
          name: '',
          age: 10,
          gender: ''
        },
        {
          name: '',
          age: 40,
          gender: ''
        },
        {
          name: '',
          age: 30,
          gender: ''
        }
      ]
    }
  },
  methods: {
    printImage() {
      printJS({
        printable: imagePath,
        type: 'image',
        showModal: false
      })
    },
    printHtml() {
      printJS({
        printable: 'htmlWrapper',
        type: 'html',
        targetStyles: ['*']
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.html-wrapper,
.image-wrapper {
  width: 80%;
  margin: 0 auto;
  & > img {
    width: 100%;
  }
}
.html-wrapper {
  margin: 0 auto;
  & > h1 {
    color: red;
  }
}
</style>
