<template>
  <div class="main-container">
    <TableHeader :can-collapsed="false">
      <template slot="right">
        <el-button
          type="primary"
          size="mini"
          icon="el-icon-plus"
          @click="onAddItem"
        >
        </el-button>
      </template>
    </TableHeader>
    <TableBody ref="tableBody">
      <template slot="table">
        <el-table
          ref="table"
          v-loading="tableLoading"
          :data="dataList"
          :header-cell-style="tableConfig.headerCellStyle"
          :size="tableConfig.size"
          :stripe="tableConfig.stripe"
          :border="tableConfig.border"
          :height="tableConfig.height"
          row-key="menuUrl"
          :tree-props="{children: 'children'}"
        >
          <el-table-column
            align="center"
            label=""
            fixed="left"
            width="80"
          >
            <template slot-scope="scope">
              {{ scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
            prop="menuName"
          />
          <el-table-column
            align="center"
            label=""
            prop="menuUrl"
          />
          <el-table-column
            align="center"
            label=""
            prop="createTime"
            width="160px"
          >
            <template slot-scope="scope">
              <div>{{ scope.row.createTime || '--' }}</div>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
          >
            <template slot-scope="scope">
              <el-link
                type="primary"
                :underline="false"
                @click="onUpdateItem(scope.row)"
              ></el-link>
              <el-link
                type="danger"
                :underline="false"
                @click="onDeleteItems(scope.row)"
              ></el-link>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </TableBody>
    <Dialog ref="dialog">
      <template>
        <el-form
          ref="baseForm"
          :model="menuModel"
          :rules="formRules"
          label-width="80px"
          label-position="right"
        >
          <el-form-item label="">
            <el-col :span="20">
              <el-select
                v-model="menuModel.parentItem"
                size="small"
                placeholder=""
                style="width: 100%"
              >
                <el-option
                  v-for="(item, index) of dataList"
                  :key="index"
                  :label="item.menuName"
                  :value="item.menuUrl"
                />
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item
            label=""
            prop="name"
          >
            <el-col :span="20">
              <el-input
                v-model="menuModel.name"
                size="small"
                placeholder=""
              />
            </el-col>
          </el-form-item>
          <el-form-item
            label=""
            prop="url"
          >
            <el-col :span="20">
              <el-input
                v-model="menuModel.url"
                size="small"
                placeholder=""
              >
                <template slot="prepend">{{ menuModel.parentItem ? menuModel.parentItem : '/' }}</template>
              </el-input>
            </el-col>
          </el-form-item>
        </el-form>
      </template>
    </Dialog>
  </div>
</template>

<script>
import TableMixin from '@/mixins/TableMixin'
import { uuid } from '@/utils/utils'
import { UpdateItemMixin, GetDataMixin, AddItemMixin, DeleteItemsMixin } from '@/mixins/ActionMixin'
export default {
  name: 'Menu',
  mixins: [TableMixin, GetDataMixin, UpdateItemMixin, AddItemMixin, DeleteItemsMixin],
  data() {
    return {
      menuModel: {
        id: uuid(),
        name: '',
        parentItem: '',
        url: '',
        createTime: ''
      },
      formRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        url: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted() {
    this.initGetData({
      url: this.$urlPath.getMenuList,
      onResult: (res) => {
        this.handleSuccess(res)
      }
    }).then(this.getData())
    this.initAddItem({
      url: this.$urlPath.getMenuList,
      params: () => {
        return {
          menuName: this.menuModel.name,
          menuUrl: this.menuModel.menuUrl
        }
      },
      onAddItem: () => {
        this.$refs.dialog.show({
          beforeShowAction: () => {
            this.menuModel.name = ''
            this.menuModel.url = ''
          },
          onConfirmCallback: () => {
            this.$refs.baseForm.validate((valid) => {
              if (!valid) return
              this.doAddItem()
            })
          }
        })
      },
      onResult: () => { },
      onError: () => { }
    })
    this.initUpdateItem({
      url: this.$urlPath.getMenuList,
      params: () => {
        return {
          menuName: this.menuModel.name,
          menuUrl: this.menuModel.menuUrl
        }
      },
      onUpdateItem: (item) => {
        this.$refs.dialog.show({
          beforeShowAction: () => {
            this.menuModel.id = item.id
            this.menuModel.name = item.menuName
            this.menuModel.url = item.menuUrl
          },
          onConfirmCallback: () => {
            this.$refs.baseForm.validate((valid) => {
              if (!valid) return
              this.doUpdateItem()
            })
          }
        })
      },
      onResult: () => {
        this.$successMsg('')
      },
      onError: () => { }
    })
    this.initDeleteItems({
      url: this.$urlPath.getMenuList,
      params: {
        id: this.menuModel.id
      },
      onDeleteItems: (item) => {
        this.menuModel.id = item.id
        if (item.children && item.children.length > 0) {
          this.$errorMsg('')
        } else {
          this.$showConfirmDialog('')
            .then(() => {
              this.$successMsg('')
            })
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.table-container {
  bottom: 8px;
}
</style>

