<template>
  <div class="main-contianer">
    <el-card
      :body-style="{padding: '15px'}"
      shadow="hover"
    >
      <el-link :underline="false"></el-link>
    </el-card>
    <el-card
      :body-style="{padding: '10px'}"
      shadow="nerve"
      class="margin-top-xs"
    >
      <div class="form-wrapper padding-top">
        <BaseForm
          ref="baseForm"
          :form-items="formItems"
          :config="formConfig"
        >
          <template #extra>
            <el-form-item
              label=""
              class="form-item"
            >
              <el-select
                v-model="joinMemeber.value"
                multiple
                placeholder=""
                style="width: 100%"
              >
                <el-option
                  v-for="item in joinMemeber.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
              class="form-item"
            >
              <el-input
                v-model="remark.value"
                placeholder=""
                type="textarea"
                :rows="3"
                style="width: 100%"
              />
            </el-form-item>
            <el-form-item>
              <div class="text-center">
                <el-button
                  type="warning"
                  size="small"
                  @click="save"
                ></el-button>
                <submit-button :on-submit="submit" />
              </div>
            </el-form-item>
          </template>
        </BaseForm>
      </div>
    </el-card>
  </div>
</template>

<script>
import BaseForm from '@/components/common/BaseForm'
export default {
  name: 'BaseFormView',
  components: { BaseForm },
  data() {
    return {
      formConfig: {
        labelWidth: 100,
        size: 'default'
      },
      formItems: [
        {
          label: '',
          type: 'input',
          name: 'name',
          value: '',
          maxLength: 50,
          inputType: 'text',
          placeholder: '',
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        },
        {
          label: '',
          type: 'input',
          name: 'content',
          value: '',
          maxLength: 10,
          inputType: 'text',
          placeholder: '',
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        },
        {
          label: '',
          type: 'date-range',
          name: 'startEndTime',
          placeholder: '',
          value: '',
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        },
        {
          label: '',
          type: 'select',
          name: 'address',
          value: '',
          placeholder: '',
          selectOptions: [
            {
              label: '',
              value: 1
            },
            {
              label: '',
              value: 2
            },
            {
              label: '',
              value: 3
            },
            {
              label: '',
              value: 4
            }
          ],
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        }
      ],
      joinMemeber: {
        value: '',
        options: [
          {
            label: '',
            value: 'zhangsan'
          },
          {
            label: '',
            value: 'lisi'
          },
          {
            label: '',
            value: 'jiangxiaoyu'
          },
          {
            label: '',
            value: 'huawuque'
          },
          {
            label: '',
            value: 'yannantian'
          }
        ]
      },
      remark: {
        value: ''
      }
    }
  },
  methods: {
    save() {
      if (this.$refs.baseForm.checkParams()) {
        if (!this.joinMemeber.value || this.joinMemeber.value.length === 0) {
          this.$errorMsg('')
          return
        }
        this.$successMsg('')
      }
    },
    submit() {
      if (this.$refs.baseForm.checkParams()) {
        if (!this.joinMemeber.value || this.joinMemeber.value.length === 0) {
          this.$errorMsg('')
          return false
        }
        return new Promise((resolve) => {
          setTimeout(() => {
            this.$successMsg('')
            resolve()
          }, 1000)
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@media screen and (max-width: 768px) {
  .form-wrapper {
    width: 100%;
    margin: 0 auto;
  }
}
@media screen and (min-width: 768px) {
  .form-wrapper {
    width: 60%;
    margin: 0 auto;
  }
}
</style>
