<template>
  <div class="main-container">
    <TableHeader :can-collapsed="false">
      <template slot="right">
        <el-button
          v-if="isInited('addItemModel')"
          type="primary"
          size="mini"
          icon="el-icon-plus"
          @click="onAddItem"
        >
        </el-button>
      </template>
    </TableHeader>
    <TableBody ref="tableBody">
      <template slot="table">
        <el-table
          ref="table"
          v-loading="tableLoading"
          :data="dataList"
          :header-cell-style="tableConfig.headerCellStyle"
          :size="tableConfig.size"
          :stripe="tableConfig.stripe"
          :border="tableConfig.border"
          :height="tableConfig.height"
        >
          <el-table-column
            align="center"
            label=""
            fixed="left"
            width="80"
          >
            <template slot-scope="scope">
              {{ scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
            prop="name"
          />
          <el-table-column
            align="center"
            label=""
            prop="roleCode"
          />
          <el-table-column
            align="center"
            label=""
            prop="description"
          />
          <el-table-column
            align="center"
            label=""
            prop="createTime"
            width="160px"
          />
          <el-table-column
            align="center"
            label=""
          >
            <template slot-scope="scope">
              <el-link
                type="primary"
                :underline="false"
                @click="onUpdateItem(scope.row)"
              ></el-link>
              <el-link
                type="danger"
                :underline="false"
                @click="onDeleteItems(scope.row)"
              ></el-link>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </TableBody>
    <Dialog ref="dialog">
      <template>
        <BaseForm
          ref="baseForm"
          :form-items="formItems"
        />
      </template>
    </Dialog>
  </div>
</template>

<script>
import TableMixin from '@/mixins/TableMixin'
import { currentDate, uuid } from '@/utils/utils'
import CRUDMixins from '@/mixins/ActionMixin'
import { formBuilder } from '@/utils/form'
import BaseForm from '@/components/common/BaseForm'
const ROLE_CODE_FLAG = 'ROLE_'
export default {
  name: 'Role',
  components: { BaseForm },
  mixins: [TableMixin, CRUDMixins],
  data() {
    return {
      roleModel: {
        id: 0,
        name: '',
        roleCode: '',
        description: '',
        createTime: ''
      }
    }
  },
  computed: {
    formItems() {
      return formBuilder()
        .formItem({
          label: '',
          type: 'input',
          name: 'name',
          value: this.roleModel.name,
          maxLength: 50,
          inputType: 'text',
          placeholder: '',
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        })
        .formItem({
          label: '',
          type: 'input',
          name: 'roleCode',
          value: this.roleModel.roleCode,
          maxLength: 20,
          inputType: 'text',
          placeholder: '',
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        })
        .formItem({
          label: '',
          type: 'input',
          name: 'description',
          value: this.roleModel.description,
          maxLength: 50,
          inputType: 'text',
          placeholder: '',
          validator: ({ value, placeholder }) => {
            if (!value) {
              this.$errorMsg(placeholder)
              return false
            }
            return true
          }
        }).build().formItems
    }
  },
  mounted() {
    this.initGetData({
      url: this.$urlPath.getRoleList,
      params: {},
      onResult: (res) => {
        this.handleSuccess(res)
      },
      onError: (error) => {
        this.$errorMsg(error)
      }
    }).then(_ => {
      this.getData()
    })
    this.initAddItem({
      url: this.$urlPath.getRoleList,
      params: () => {
        return this.$refs.baseForm.checkParams()
      },
      onAddItem: () => {
        this.$refs.dialog.show({
          beforeShowAction: () => {
            this.roleModel = {
              name: '',
              roleCode: '',
              description: ''
            }
          },
          onConfirmCallback: () => {
            const checkResult = this.$refs.baseForm.checkParams()
            if (checkResult) {
              this.doAddItem()
            }
          }
        })
      },
      onResult: (res) => {
        const item = this.$refs.baseForm.generatorParams()
        console.log(item)
        if (!item.roleCode.startsWith(ROLE_CODE_FLAG)) {
          item.roleCode = ROLE_CODE_FLAG + item.roleCode
        }
        item.itemId = uuid()
        item.createTime = currentDate()
        this.dataList.unshift(item)
        this.$refs.dialog.close()
      },
      onError: (error) => {
        this.$errorMsg(error)
      }
    })
    this.initUpdateItem({
      url: this.$urlPath.getRoleList,
      params: () => {
        return this.$refs.baseForm.checkParams()
      },
      onUpdateItem: (item) => {
        this.$refs.dialog.show({
          beforeShowAction: () => {
            this.roleModel.name = item.name
            this.roleModel.roleCode = item.roleCode
            this.roleModel.description = item.description
          },
          onConfirmCallback: () => {
            const checkResult = this.$refs.baseForm.checkParams()
            if (checkResult) {
              this.doUpdateItem()
            }
          }
        })
      },
      onResult: () => {
        this.$successMsg('')
        this.$refs.dialog.close()
      }
    })
    this.initDeleteItems({
      url: this.$urlPath.getRoleList,
      params: {},
      onDeleteItems: (item) => {
        this.roleModel = item
        this.$showConfirmDialog('').then((_) => {
          this.doDeleteItems()
        })
      },
      onResult: () => {
        this.dataList.splice(this.dataList.indexOf(this.roleModel), 1)
        this.$successMsg('')
      },
      onError: () => {
        this.$successMsg('')
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.table-container {
  bottom: 8px;
}
</style>

