<template>
  <div class="main-container">
    <div class="editor-container">
      <MarkdownEditor
        ref="markdownEditor"
        v-model="editorText"
        :height="400"
        placeholder="Insert here"
      />
    </div>
    <div class="margin-top-lg">
      <el-button
        size="small"
        @click="addText"
      ></el-button>
      <el-button
        size="small"
        @click="addImage"
      ></el-button>
      <el-button
        size="small"
        type="primary"
        @click="getHTML"
      >HTML</el-button>
    </div>
    <div
      v-if="priviewContent"
      class="priview-content padding-xs margin-top"
      v-html="priviewContent"
    ></div>
  </div>
</template>

<script>
import MarkdownEditor from '@/components/common/MarkdownEditor'
import SimpleMDE from 'simplemde'

export default {
  name: 'Markdown',
  components: {
    MarkdownEditor
  },
  data() {
    return {
      editorText: '',
      priviewContent: ''
    }
  },
  methods: {
    addText() {
      this.$refs.markdownEditor.addText('\n### ')
    },
    addImage() {
      this.$refs.markdownEditor.addImage(
        '\n![](https://file.iviewui.com/dist/2ecd3b0452aa197097d5131afacab7b8.png)'
      )
    },
    getHTML() {
      this.priviewContent = SimpleMDE.prototype.markdown(this.editorText)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';
.editor-container {
  position: relative;
}
.theme-dark .priview-content {
  background: #5a5a5a;
  color: #fff;
}
.priview-content {
  background: #fff;
  color: #333;
}
</style>
