<template>
  <div class="main-container">
    <el-card :body-style="{padding: 0}">
      <div slot="header">
        <el-link
          :underline="false"
          type="primary"
        ></el-link>
      </div>
      <div class="padding">
        <el-button
          size="small"
          type="primary"
          plain
          @click="openNotify('primary')"
        ></el-button>
        <el-button
          size="small"
          type="success"
          plain
          @click="openNotify('success')"
        ></el-button>
        <el-button
          size="small"
          type="warning"
          plain
          @click="openNotify('warning')"
        ></el-button>
        <el-button
          size="small"
          type="danger"
          plain
          @click="openNotify('danger')"
        ></el-button>
        <el-button
          size="small"
          type="info"
          plain
          @click="openNotify('html')"
        >HTML</el-button>
      </div>
    </el-card>
    <el-card :body-style="{padding: 0}">
      <div slot="header">
        <el-link
          :underline="false"
          type="primary"
        ></el-link>
      </div>
      <div class="padding">
        <el-button
          size="small"
          type="primary"
          plain
          @click="openConfirm('primary')"
        ></el-button>
        <el-button
          size="small"
          type="warning"
          plain
          @click="openConfirm('warning')"
        ></el-button>
        <el-button
          size="small"
          type="info"
          plain
          @click="openConfirm('html')"
        >HTML</el-button>
      </div>
    </el-card>
    <el-card :body-style="{padding: 0}">
      <div slot="header">
        <el-link
          :underline="false"
          type="primary"
        ></el-link>
      </div>
      <div class="padding">
        <el-button
          size="small"
          type="primary"
          plain
          @click="openMessage('primary')"
        ></el-button>
        <el-button
          size="small"
          type="success"
          plain
          @click="openMessage('success')"
        ></el-button>
        <el-button
          size="small"
          type="warning"
          plain
          @click="openMessage('warning')"
        ></el-button>
        <el-button
          size="small"
          type="danger"
          plain
          @click="openMessage('danger')"
        ></el-button>
        <el-button
          size="small"
          type="info"
          plain
          @click="openMessage('html')"
        >HTML</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Tip',
  methods: {
    openNotify(type) {
      switch (type) {
        case 'primary':
          this.$notify({
            title: '',
            message: ''
          })
          break
        case 'success':
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
          break
        case 'warning':
          this.$notify({
            title: '',
            message: '',
            type: 'warning'
          })
          break
        case 'danger':
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
          break
        case 'html':
          this.$notify({
            title: '',
            dangerouslyUseHTMLString: true,
            message: `<ul>
            <li style="color: red"></li>
            <li style="font-weight: bold"></li>
            <li style="font-size: 20px"></li>
            </ul>`
          })
          break
      }
    },
    openConfirm(type) {
      switch (type) {
        case 'primary':
          this.$alert('', '', {
            confirmButtonText: '',
            callback: action => {
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
            }
          })
          break
        case 'warning':
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$message({
              type: 'success',
              message: '!'
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            })
          })
          break
        case 'html':
          this.$alert(`<ul>
            <li style="color: red"></li>
            <li style="font-weight: bold"></li>
            <li style="font-size: 20px"></li>
            </ul>`, '', {
            dangerouslyUseHTMLString: true
          })
          break
      }
    },
    openMessage(type) {
      switch (type) {
        case 'primary':
          this.$message('10+')
          break
        case 'success':
          this.$message({
            message: '',
            type: 'success'
          })
          break
        case 'warning':
          this.$message({
            message: '',
            type: 'warning'
          })
          break
        case 'danger':
          this.$message({
            message: '',
            type: 'error'
          })
          break
        case 'html':
          this.$message({
            dangerouslyUseHTMLString: true,
            message: `<ul>
            <li style="color: red"></li>
            <li style="font-weight: bold"></li>
            <li style="font-size: 20px"></li>
            </ul>`
          })
          break
      }
    }
  }
}
</script>

<style scoped>
.el-card + .el-card {
  margin-top: 10px;
}
</style>
